@import url(https://fonts.googleapis.com/css?family=Julius+Sans+One&display=swap);




.board{display:flex;flex-direction:column;outline:none;height:calc( 100vh - (10vh + 9vh + 10vh)+20vh);margin:10px 0px}.board__row{display:flex;flex-direction:row;justify-content:center}

.board__item{width:calc(calc(calc( 100vh - (10vh + 9vh + 10vh)+20vh) / 16) - 5px);height:calc(calc(calc( 100vh - (10vh + 9vh + 10vh)+20vh) / 16) - 5px);margin:3px;border-radius:3px;box-shadow:0 2px 0 #756c6c;cursor:pointer;transition:background-color 0.3s ease-out}

.modal-info__content{font-size:20px}.modal-info{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:400px;height:200px;background-color:white;border-radius:5px;outline:none;box-shadow:0 3px 0 0 rgba(0,0,0,0.4);text-align:center;display:flex;flex-direction:column;justify-content:center;font-weight:bold;border-top:7px solid #4263eb;width:700px;height:350px;padding:20px;align-items:center}.modal-info__title{font-family:inherit;font-size:36px;margin-bottom:20px;color:#4263eb}.modal-info__color{margin-bottom:20px}.modal-info__usage .modal-info__row{margin-bottom:20px}.modal-info__usage .modal-info__square,.modal-info__usage .modal-info__square--initial,.modal-info__usage .modal-info__square--visited,.modal-info__usage .modal-info__square--clicked,.modal-info__usage .modal-info__square--fixed,.modal-info__usage .modal-info__square--shortest{margin-right:10px}.modal-info__usage h2{margin-right:10px;font-size:20px}.modal-info__row{display:flex;align-items:center}.modal-info__content{width:150px;display:flex;flex-direction:row;align-items:center;margin-bottom:20px}.modal-info__square,.modal-info__square--initial,.modal-info__square--visited,.modal-info__square--clicked,.modal-info__square--fixed,.modal-info__square--shortest{display:block;width:24px;height:24px;margin-right:20px}.modal-info__square--initial{background-color:#ada8a8}.modal-info__square--visited{background-color:rgba(43,139,230,0.741176)}.modal-info__square--clicked{background-color:#ff5100}.modal-info__square--fixed{background-color:rgba(80,80,80,0)}.modal-info__square--shortest{background-color:#f04460}.modal-info__close{position:absolute;right:10px;top:10px;border-radius:50%;width:25px;height:25px;border:none;background-color:#4263eb;color:white;cursor:pointer;transition:opacity 0.3s linear;opacity:0.5;outline:none}.modal-info__close:hover{opacity:1}

.modal-error__content{font-size:20px}.modal-error{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:400px;height:200px;background-color:white;border-radius:5px;outline:none;box-shadow:0 3px 0 0 rgba(0,0,0,0.4);text-align:center;display:flex;flex-direction:column;justify-content:center;font-weight:bold;border-top:7px solid #f03e3e}.modal-error__title{font-family:inherit;font-size:36px;margin-bottom:20px;color:#f03e3e}.modal-error__close{position:absolute;right:10px;top:10px;border-radius:50%;width:25px;height:25px;border:none;background-color:#f03e3e;color:white;cursor:pointer;transition:opacity 0.3s linear;opacity:0.5;outline:none}.modal-error__close:hover{opacity:1}

body,html{height:100%}body{font-family:'Julius Sans One', sans-serif;-ms-user-select:none;user-select:none;-webkit-user-select:none;-moz-user-select:none}

